import React, {useState} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import './App.scss';
import MenuList from './Components/list-menu';
import Footer from './Components/footer';
import Home from './Components/home/Home';
import MyMeal from './Components/day-001/MyMeal';
import Count from './Components/day-002/Count';

function App() {
  let data = [
    {
       "project_id": "000",
       "project_name": "Home",
       "project_component": Home,
       "project_url": ""
     },
    {
       "project_id": "001",
       "project_name": "Random Meal",
       "project_component": MyMeal,
       "project_url": "day-001"
     },
    {
       "project_id": "002",
       "project_name": "Visitor Count",
       "project_component": Count,
       "project_url": "day-002"
     }
 ];
  return (
    <div className="App">
      <Router basename="/coding-challenge">
        <>
          <MenuList menu={data} />
          <Switch>            
            {
              data.map((value, key)=>(
                // console.log(value.project_component),
                <Route exact path={`/${value.project_url}`} key={key}>
                  {
                    React.createElement(value.project_component, {
                      to : value.url, number : value.project_id 
                    })
                  }
                </Route>
              ))
            }
          </Switch>
          <Footer />
        </>
      </Router>
    </div>
  );
}

export default App;
